.gh-postfeed .post {
    display: block;
}

.gh-postfeed .post-card:nth-child(n+2) {
    margin-top: 3em;
}

.gh-postfeed .post span {
    color: var(--ghost-accent-color);
    text-decoration: underline;
}







.gh-author-image {
    height: 12vmin;
    width: 12vmin;
    margin: 0 auto 1.5em;
    border-radius: 100%;
    overflow: hidden;
    object-fit: cover;
}

.gh-author-meta {
    margin: 2vmin 0 0 0;
    font-size: 1.2em;
}

.gh-author-links {
    display: flex;
    align-items: center;
    justify-content: center;
}

.gh-author-links a {
    position: relative;
    display: inline-flex;
    align-items: center;
    margin: 0 0.75vmin;
}

.gh-author-links a + a:before {
    display: block;
    content: "";
    margin: 0 1.25vmin 0 0;
    height: 1em;
    width: 1px;
    background: rgba(0,0,0,0.3);
    transform: rotate(20deg);
}

@media (max-width: 700px) {
    .gh-author-meta {
        font-size: 1.6rem;
    }
}






.gh-pagination {
    width: 100%;
    max-width: 600px;
    padding: 40px var(--margin) 0;
    margin: 40px auto 0;
    border-top: var(--color-border) 1px solid;
    text-align: center;
}

.gh-pagination .newer-posts {
    margin-right: 1em;
}

.gh-pagination .older-posts {
    margin-left: 1em;
}

.gh-pagination a {
    color: var(--ghost-accent-color);
}
